import React, { Component } from 'react'

const images = [
	 require('./news-images/hackathon_web.png')
];

const description = [
	""
];

const dates = [
	"January 3rd-5th, 2019"
];

class NewsEvents extends React.Component {
	constructor(props){
		super(props)
		this.state = {
			image: images[0],
			description: description[0],
			dates: dates[0],
			number: 0
		};

		this.setEvent = this.setEvent.bind(this);
		this.launchNewsEvents = this.launchNewsEvents.bind(this)
	}

	setEvent = () => {

		(this.state.number <= images.length - 1) ?
			this.setState({
				image: images[this.state.number],
				description: description[this.state.number],
				dates: dates[this.state.number],
				number: this.state.number + 1
			}, function(){console.log(this.state.image)})
		:   
			this.setState({
				number: 0,
				image: images[0],
				description: description[0],
				dates: dates[0],
			}, function(){console.log(this.state.image)})
	};


	launchNewsEvents = () => {
		setInterval(() => {
			this.setEvent();
		}, 10000)
	}

	componentDidMount() {
		this.launchNewsEvents();
	};

	render(){
		return (
			<div id = "newsEvents" className = "flex flex-row justify-center fruition-gray white pv2 mr3">
				<div className = "w-90 h-100"><img className = "h-100 w-100" src={this.state.image}/></div>
				{/*<div className = "w-40 h-auto flex flex-column justify-around f3">
					<p>{this.state.description}</p>
					<p>{this.state.dates}</p>
				</div>*/}
			</div>
		)
	}
}

export default NewsEvents