import React from 'react';
import {Link, Route, Switch} from 'react-router-dom'

const AboutHeader = () => {
	return(
	<div>
		<div className = "bg-white db bb b--black-10 about-header pt5 relative">
			<div className = "flex flex-row justify-start items-center w-100 h-100">
				<div className="w-50 h-100 flex justify-center items-center">
					<div className="ma3 w-80">
						<h2>Fruition understands the power behind developing your unique idea into a business that provides a sustainable benefit to customers, commits to the growth of its people and partners with others that share their values and vision. </h2>
						<Link className = "linkButtonContact white no-underline" to="/Contact"><a className="w4 f6 link ph3 pv3 mb4 dib white ma3 tc hvr-shutter-out-verticalBlue">Contact Us</a></Link>
					</div>
				</div>
				<img className="w-40" src={require('../images/FruitionLogo_full_color.png')} />
			</div>		
		</div>
	</div>
	)
}

export default AboutHeader