import React, { Component } from 'react';
import TestText from './TestText';
import TestimonialButtons from './TestimonialButtons';

const quotes = [
	{
		author: "Kathy Schwartz",
		content: "I recently had the opportunity to talk with Wesley about my small business. I came away inspired and energized. He immediately grasped what was unique about my small business and had excellent ideas about how to move my business forward."
	},
	{
		author: "Basil Nyathi",
		content: "It's been an amazing few months working with Wes and the team at fruition. Thank you for the guidance, direction and support. I would recommend you to anyone in a heart beat."
	},
	{
		author: "Michael Curran",
		content: "Working with Fruition Technology Labs and Wesley Okeke has been a great help to my venture Guide Change.  In addition to their focus on social impact issues, what sets Fruition apart from other incubator programs is the one-on-one mentoring that start-ups receive.  Through the personalized advice offered by Wesley and Fruition, Guide Change is making great progress in our fight to end elder financial abuse."
	},
	{
		author: "Joshua Webb",
		content: "There are a lot of places you can find information, there are many who will give an opinion, but for me one of the most important things is to find people with whom you share a similar mission and vision, creative culture and sense of community. Wes and the Fruition team have managed to cultivate that triple threat and are poised to help many entrepreneurs and businesses grow! I challenge you to contact them today with your idea. Remember 'someday' is not a day of the week, get going on your tomorrow today."
	},
	{
		author: "Christian Doyle",
		content: "Wes and the whole team at Fruition are some of the most talented and motivated individuals in the Houston area! If you are an aspiring entrepreneur I suggest you check these guys out!"
	}
];

const testimonySize = {
	"width": "700px",
	"height": "450px"
}

class Testimonials extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			content: quotes[0].content,
			author: quotes[0].author,
			number: 0
		}
		this.newQuote = this.newQuote.bind(this);
		this.prevQuote = this.prevQuote.bind(this);
		this.launchTestimony = this.launchTestimony.bind(this);

		let timer = setInterval(() => {
			this.newQuote();
			}, 5000)
		}


	newQuote = () => {
		
		if (this.state.number < quotes.length -1 ) {
			this.setState( currentState => ({number: this.state.number + 1}), function() {
				this.setState( currentState => ({content: quotes[this.state.number].content}), function(){
					this.setState( currentState => ({author: quotes[this.state.number].author}), function(){
					});
				});	
			});
			clearInterval()
			console.log("boop");
		}	
		else {
			this.setState( currentState => ({number: 0}), function() {
				this.setState( currentState => ({content: quotes[this.state.number].content}), function(){
					this.setState( currentState => ({author: quotes[this.state.number].author}), function(){
					});
				});	
			});
		}
	};

	prevQuote() {
		
		if (this.state.number > 0 ) {
			
			this.setState( currentState => ({number: this.state.number - 1}), function() {
				this.setState( currentState => ({content: quotes[this.state.number].content}), function(){
					this.setState( currentState => ({author: quotes[this.state.number].author}), function(){
					});
				});	
			});
		}

		else {
			this.setState( currentState => ({number: quotes.length - 1}), function() {
				this.setState( currentState => ({content: quotes[this.state.number].content}), function(){
					this.setState( currentState => ({author: quotes[this.state.number].author}), function(){
					});
				});	
			});
		}
	};

	launchTestimony = () => {
		setInterval(() => {
			this.newQuote();
		}, 5000)
	}

	componentDidMount(){
		this.launchTestimony();
	}

	render() {
		return (
			<div style={testimonySize} id = "testimonial-box" className = "pa3 ma4 flex flex-column justify-center items-center ba bw1 fruition-blue">
				<TestText quote = {this.state.content} author = {this.state.author} />
				<TestimonialButtons newQuote={this.newQuote} prevQuote={this.prevQuote}/>
			</div>
			)
	}
}

export default Testimonials