import React from 'react';
import NavBar from '../NavBar';
import PrototypeHeader from './PrototypeHeader';
import PrototypeMission from './PrototypeMission';
import PrototypePanels from './PrototypePanels';
import Footer from '../Footer'

const Prototype = () => {
	return(
	<div>
		<NavBar className = "mb2" />
		<PrototypeHeader />
		<PrototypeMission />
		<PrototypePanels />
		<Footer />
	</div>
	)
}

export default Prototype;