import React from 'react';

const cogContainer = {
	"width": "1000px"
}

const Cog = ({ cogToggleUnlock, cogToggleExplore, cogTogglePlan, cogToggleBuild, cogToggleLaunch, cogState, cogState2, cogState3, cogState4, cogState5, plan1State, plan1StateChange, plan2State, plan2StateChange, plan3State, plan3StateChange, plan4State, plan4StateChange, plan5State, plan5StateChange, plan6State, plan6StateChange }) => {
	return (
		<div className=''>
			<div className = 'flex flex-row justify-center coglist pa5'>
				{(cogState === false) 
				?	
				<div onClick={cogToggleUnlock} id="unlock" className="cog pointer grow flex justify-center items-center">
					<div className="spoke"></div>
					<div className="spoke"></div>
					<div className="spoke"></div>
					<div className="spoke"></div>
					<div className="spoke"></div>
					<div className="spoke"></div>
					<div id ="example" className="step">	
					<p className='mb2 white small-font'>Unlock</p>
					</div>
				</div>
				:
				<div onClick={cogToggleUnlock} id="unlock" className="cog pointer grow flex justify-center items-center">
					<div className="spokeUnlock"></div>
					<div className="spokeUnlock"></div>
					<div className="spokeUnlock"></div>
					<div className="spokeUnlock"></div>
					<div className="spokeUnlock"></div>
					<div className="spokeUnlock"></div>
					<div id ="example" className="step">
					<p className='mb2 white cog-font'><u className="f5">Unlock</u> <br />Unlocking the most effective Value Proposition 
					that will create your products powerful,  unique and competitive  fingerprint.</p>
					</div>
				</div>
				}	
				<i id="x1" class="fa fa-times remove x-position relative pointer" aria-hidden="true"></i>
				<div onClick={cogToggleExplore} id="explore" className="cog cogEx grow pointer flex justify-center items-center fruition-blue">
					<div className= "spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="step">
					{(cogState2 === false)
					?<p className='mb2 white small-font'>Explore</p>
					:<p className='mb2 white cog-font'><u className="f5">Explore</u> <br />Explore the market - that your <br/> product must compete in. 
					Work with Fruitions team to prepare yourself <br /> and your product for success</p>	
					}
					</div>
				</div>
				<i id="x2" class="fa fa-times remove x-position relative pointer" aria-hidden="true"></i>
				<div>
					<div onClick={cogTogglePlan} id="plan" className="cog cogEx grow pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step">
						{(cogState3 === false)
							?<p className='mb2 white small-font'>Plan</p>
							:<p className='mb2 white plan-font'><u className="f5">Plan</u> <br />Plan out With a diverse <br /> core team, plan how your product <br /> 
							will carefully address <br /> the pain that your customers may 
							currently be experiencing and at the same evaluate
							 how to incorporate disruptive technologies</p>
							}
						</div>
					</div>
					<div id="plan1" onMouseEnter={plan1StateChange} onMouseLeave={plan1StateChange} className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step">
						{(plan1State === false)
							?<p className='mb2 white'>GPS Tracking</p>
							:<p className='mb2 white subPlan-font'><br />Location tracking have <br />become a necessity<br /> in modern age<br /> and with
							the advent of wearable computing, incorporating these<br /> technologies in such devices 
							would become a norm<br /> in few  years.</p>
							}
						</div>
					</div>
					<div id="plan2" onMouseEnter={plan2StateChange} onMouseLeave={plan2StateChange} className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step">
						{(plan2State === false)
						?<p className='mb2 white f6'>Extended Reality</p>
						:<p className='mb2 white subPlan-font'>Technologies creating<br /> bridge between humans and machines,<br /> blending and creating new<br /> realities, 
						bringing the<br /> communities closer and forging<br /> a new path towards<br /> a better future.</p>
						}
						</div>
					</div>
					<div id="plan3" onMouseEnter={plan3StateChange} onMouseLeave={plan3StateChange} className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step">
						{(plan3State === false)
						?<p className='mb2 white'>Big Data</p>
						:<p className='mb2 white subPlan-font'>Data analytics enhances<br /> decision making process and minimizes 
						the risk thereby fostering beneficial technologies and products.</p>
						}
						</div>
					</div>
					<div id="plan4" onMouseEnter={plan4StateChange} onMouseLeave={plan4StateChange} className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step">
						{(plan4State === false)
						?<p className='mb2 white'>Blockchain</p>
						:<p className='mb2 white subPlan-font'>Customer privacy is<br /> an important issue for growing technologies in the Information Age
						 and a decentralised<br /> framework linked with<br /> cryptography might be an apt solution to it.</p>
						}
						</div>
					</div>
					<div id="plan5" onMouseEnter={plan5StateChange} onMouseLeave={plan5StateChange} className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step">
						{(plan5State === false)
						?<p className='mb2 white'>IOT</p>
						:<p className='mb2 white subPlan-font'>Internet doesnt seem<br /> a big thing where there are WIFI hotspots everywhere but for some having
						 access to even Internet seems to be a luxurious affair and that needs to change.</p>
						}
						</div>
					</div>
					<div id="plan6" onMouseEnter={plan6StateChange} onMouseLeave={plan6StateChange} className="plan-cog remove cogEx pointer flex justify-center items-center fruition-blue">
						<div className= "spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="spoke fruition-blue"></div>
						<div className="step">
						{(plan6State === false)
						?<p className='mb2 white'>Artificial Intelligence</p>
						:<p className='mb2 white subPlan-font'>During the times where<br /> AI is being disputed as being<br /> good or evil; aggressive efforts need
						 to be made to present an alternative to this narrative to help the present and preserve the future.</p>
						}
						</div>
					</div>
				</div>
				<i id="x3" class="fa fa-times remove x-position relative pointer" aria-hidden="true"></i>
				<div onClick={cogToggleBuild} id="build" className="cog cogEx grow pointer flex justify-center items-center fruition-blue">
					<div className= "spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="step">
					{(cogState4 === false)
					?<p className='mb2 white small-font'>Build</p>
					:<p className='mb2 white cog-font'><u className="f5">Build</u> <br />Your team of creative <br /> and technological collaborators
					help you actually design and create your MVP <br /> ( minimum viable product ).</p>
					}
					</div>
				</div>
				<i id="x4" class="fa fa-times remove x-position relative pointer" aria-hidden="true"></i>
				<div onClick={cogToggleLaunch} id="launch" className="cog cogEx grow pointer flex justify-center items-center fruition-blue">
					<div className= "spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="spoke fruition-blue"></div>
					<div className="step"><p className='mb2 white small-font'>Launch</p></div>
				</div>
				<i id="x5" class="fa fa-times remove x-position relative pointer" aria-hidden="true"></i>	
			</div>
		</div>
	);
}

export default Cog
		