import React from 'react';

const style = {
	height: "350px",
	width: "400px"
}

const imgStyle = {
	height: "200px",
	width: "200px"
}

const Card = ({ name, position, bio, image }) => {
	return (
		<div style={style} className='tc bg-near-white dib br3 pa3 ma2 grow bw2 shadow-5'>
			<img style={imgStyle} alt={name} src={image}/>
			<h2 className="ma3">{name}</h2>
			<h3>{position}</h3>
		</div>
	)
}

export default Card;