import React from 'react';
import NavBar from '../NavBar';
import ContactInfo from './ContactInfo';
import Message from './Message';
import Footer from '../Footer'


class Contact extends React.Component {
	constructor(props){
		super(props)
	}

	render(){
		return(
			<div>
				<NavBar />
				<div id="contact-header" className = "db bb b--black-10 pt5 fruition-black relative"></div>
				<div className='flex flex-column justify-center items-center fruition-blue white mb3 pa3'>
					<h2>Contact Us :</h2>
				</div>
				<div className="pa3 tc">
					<p>If you have any questions or comments regarding Fruition Technology Labs process, services, people, facilities or new Lab, 
					please do not hesitate to contact us!</p>
				</div>
				<div className="flex flex-row justify-around flex-wrap">
					<Message />
					<ContactInfo />	
				</div>
				<div className="pb5 mb5"></div>
				<Footer />
			</div>
			)
	}
}

export default Contact