import React, { Component } from 'react';
import NavBar from '../NavBar'
import peopleList from './peopleList';
import CardList from './CardList';
import Footer from '../Footer'

class People extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			people: peopleList
		}
	}

	render(){

		const { people } = this.state;

		return(
		<div className="db">
			<NavBar />
			<div className = "bg-white db bb b--black-10 people-header pt5 relative"></div>
			<div className='flex flex-column justify-center items-center fruition-blue white mb3 pa3'>
				<h2>People</h2>
			</div>
			<div className="">
				<CardList people={ people } />
			</div>
			<Footer />

		</div>
		)
	}
}

export default People