import React, { Component } from 'react';
import {Link, Route, Switch} from 'react-router-dom';
import Home from './components/home/Home';
import Entrepreneurs from './components/entrepreneur/Entrepreneurs';
import Engineers from './components/engineer/Engineers';
import Prototype from './components/prototype/Prototype';
import Businesses from './components/business/Businesses';
import About from './components/about/About';
import People from './components/people/People';
import Contact from './components/contact/Contact'
import './App.css';

class App extends Component {
    constructor() {
        super();
    };

  render() {
    return (
        <div>
            <Route exact path="/" component={Home}/>
            <Route path="/Entrepreneurs" component={Entrepreneurs}/>
            <Route path="/Engineers" component={Engineers}/>
            <Route path="/Businesses" component={Businesses}/>
            <Route path="/About" component={About}/>
            <Route path="/People" component={People}/>
            <Route path="/Contact" component={Contact}/>
            <Route path="/Prototype" component={Prototype}/>
        </div>
    );
  }
}

export default App;
