import React from 'react';
import Card from './Card';

const CardList = ({ people }) => {

	return(
		<div className="flex flex-row justify-center flex-wrap ma5">
		{ people.map((people, i) => {
			return(
				<Card 
				name={people.name}
				position={people.position}
				bio={people.bio}
				image={people.image}
				/>
				)
		})}			
		</div>
		)
};

export default CardList