import React from 'react';

const messageStyle = {
	"width": "800px"
}

const Message = () => {
	return(
		<div style={messageStyle} className="flex flex-column ma3">
			<h4>Get in touch</h4>
			<div>
				<form>
					<div id="message-container" className="flex flex-row flex-wrap">
						<div>
							First Name:<br />
							<input type="text" name="name" /><br /> 
							Last Name:<br />
							<input type="text" name="email" /><br />
							Subject:<br />
							<input type="text" name="subject" /><br />
						</div>
						<div className="ml4 flex flex-column">
							Message:<br />
							<textarea rows="6" cols="70" name="message" />
						</div>
					</div>
					<div className="mt5">
						<input className="hvr-shutter-out-vertical white pointer pa4" type="submit" name="submit" />
					</div>		
				</form>
			</div>
		</div>
	)
}

export default Message