import React from 'react';

const Header = () => {
	return (
		<div className = "bg-white flex flex-row justify-around items-center bb b--black-10 home-header pt5 relative">
			<div  className = 'flex flex-column justify-center items-center'>
				<div id="header-text" className = 'bb'>
					<p className='fw6 bold'>This is more than just a thought.<br />
					This is your great idea!</p>
					<p className='f3'>Turn your tech idea into the perfect purpose venture</p>
				</div>
				<a href ='#'><img src = {require("./fruition-logo.png")} className ='h4 db pt2'/></a>	
			</div>
			<div className = 'h-100 flex items-center'>
				<img className="h-100" src={require("./thinking-woman.png")} alt="stock-image" id="header-photo" />
			</div>
		</div>	
		);
}

export default Header;