import React, { Component } from 'react';

class TestText extends React.Component {
	constructor(props) {
		super(props)
	}

	render(){
		return(
			<div className = "white">
				<h4 id = "text"><i>"{this.props.quote}"</i></h4>
				<h5 id = "author">- {this.props.author}</h5>
			</div>
			)
	}
}

export default TestText